

@font-face {
    font-family: 'Roboto_regular_bold';
    src: url('/public/assets/fonts/Roboto-Regular.ttf') format('truetype');
    src: url('/public/assets/fonts/Roboto-Bold.ttf') format(('truetype'));
    font-weight: normal;
    font-style: normal;
}

.main-text {
    padding: 10% 0% 4% 8%;
}
h1 {
    font-family: 'Roboto_regular_bold';
    font-size: clamp(30px, 3vw, 50px);
    font-weight: bold;
    line-height: 40px;
    color: hsl(234, 29%, 20%);
}

p {
    font-family: 'Roboto_regular_bold';
    font-size: clamp(12px, 3vw, 18px);
    font-weight: bold;
    width: 100%;
    color: hsl(234, 29%, 20%);
}