
@font-face {
    font-family: 'Roboto_regular_bold';
    src: url('/public/assets/fonts/Roboto-Bold.ttf') format('truetype');
    src: url('/public/assets/fonts/Roboto-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

#Inputs > form > label {
    font-family: 'Roboto_regular_bold';
    font-weight: 600;
    color: hsl(234, 29%, 20%);
}

#Inputs > form > input {
    margin-top: 2%;
    width: 80%;
    padding: 4% 0% 4% 4%;
    border: 1px solid hsl(231, 7%, 60%);
    border-radius: 10px;
    outline: none;
}

#Inputs > form > input:focus {
    border: 1px solid black;
}

#Inputs > form > button {
    font-family: 'Roboto_regular_bold';
    font-size: 16px;
    font-weight: 600;
    color: white;
    margin-top: 4%;
    width: 85%;
    padding: 4%;    
    border-radius: 10px;
    border: 1px solid hsl(234, 29%, 20%);
    background-color: hsl(234, 29%, 20%);
    transition: background-color 0.4s ease;
}

#Inputs > form > button:hover {
    background-color: hsl(4, 100%, 67%);
    box-shadow: 0px 26px 15px -3px rgba(0,0,0,0.1);
    border: none;
}

@media screen and (min-width: 375px) and (max-width: 500px) {
    #Inputs > form > input {
        margin-left: 1%;
        width: 78vw;
    }

    #Inputs > form > button {
        margin-left: 1%;
        width: 89%;
    }
}