
@font-face {
    font-family: 'Roboto_regular_bold';
    src: url('/public/assets/fonts/Roboto-Regular.ttf') format('truetype');
    src: url('/public/assets/fonts/Roboto-Bold.ttf') format(('truetype'));
    font-weight: normal;
    font-style: bold;
}

.success-main {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.content {
    padding: 2%;
    background-color: white;
    height: 244px;
    width: 300px;
    border-radius: 20px;
}

.content img {
    width: 40px;
    height: 40px;
}

.content h2 {
    margin: 12px auto;
    font-family: 'Roboto_regular_bold';
    font-weight: bold;
    font-size: clamp(20px, 3vw, 30px);
    color: hsl(234, 29%, 20%);
}

.content p {
    font-family: 'Roboto_regular_bold';
    font-size: clamp(8px, 3vw, 12px);
    color: hsl(234, 29%, 20%);  
}

.content .button-section {
    display: flex;
    justify-content: center;
}

.content .button-section button {
    width: 100%;
    padding: 4%;
    border-radius: 8px;
    background-color: hsl(234, 29%, 20%);
    border: 1px solid hsl(234, 29%, 20%);
    color: white;
    font-family: 'Roboto_regular_bold';
    z-index: 1;
    box-shadow: 0px 26px 15px -3px rgba(0,0,0,0.1);
    transition: background-color 0.4s ease; 
}

.content .button-section button:hover {
    background-color: hsl(4, 100%, 67%);
    box-shadow: 0px 26px 15px -3px rgba(0,0,0,0.1);
    border: none;
}

@media screen and (min-width: 375px) and (max-width: 500px) {
    .content {
        padding-top: 4%;
        max-height: 40vh;
    }
}