#Sign-up-image-left-image div .desk-img {
    width: 100%;
    height: 100vh;
}

.mobl-img {
    display: none;
}

@media screen and (min-width: 375px) and (max-width: 500px) {
    .desk-img {
        display: none;
    }
    .mobl-img {
        display: block;
        width: 100%;
    }
}